.touch-info {
  background-color: #000000;
  padding: 20px;
  border-radius: 8px;
  width: 460px;
  height: auto;
  margin-bottom: 20px;
}

.touch-info h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
  color: #FFFFFF;
}

.touch-bar {
  display: flex;
  align-items: center;
  margin: 5px 0;
  color: #FFFFFF;
}

.bar {
  height: 15px;
  margin: 0 10px;
  border-radius: 5px;
  background-color: grey; /* 기본 배경색 */
}

.red {
  background-color: red;
}

.green {
  background-color: green;
}

.blue {
  background-color: blue;
}

.purple {
  background-color: purple;
}
