import React from 'react';
import '../css/TouchInfo.css';

const TouchInfo = () => {
  // 임시로 사용할 촉감 데이터 (1~5점)
  const touchData = {
    hardness: 3, // 딱딱함 정도
    roughness: 2, // 거칠음 정도
    thickness: 4, // 두꺼움 정도
    elasticity: 5, // 신축성 정도
  };

  // 메인 컬러와 점수에 따른 투명도 설정 함수
  const getColorWithOpacity = (color, score) => {
    const opacity = score / 5;
    return `${color}${Math.floor(opacity * 255).toString(16).padStart(2, '0')}`;
  };

  return (
    <div className="touch-info">
      <h2>촉감 정보</h2>
      <div className="touch-bar">
        <span>딱딱한</span>
        <div className="bar" style={{ backgroundColor: getColorWithOpacity('#ff0000', touchData.hardness), flex: touchData.hardness }}></div>
        <span>부드러운</span>
      </div>
      <div className="touch-bar">
        <span>거친</span>
        <div className="bar" style={{ backgroundColor: getColorWithOpacity('#ffa500', touchData.roughness), flex: touchData.roughness }}></div>
        <span>매끄러운</span>
      </div>
      <div className="touch-bar">
        <span>얇은</span>
        <div className="bar" style={{ backgroundColor: getColorWithOpacity('#0000ff', touchData.thickness), flex: touchData.thickness }}></div>
        <span>두꺼운</span>
      </div>
      <div className="touch-bar">
        <span>신축성 없는</span>
        <div className="bar" style={{ backgroundColor: getColorWithOpacity('#800080', touchData.elasticity), flex: touchData.elasticity }}></div>
        <span>신축성 있는</span>
      </div>
    </div>
  );
};

export default TouchInfo;
